import React from 'react';
import s from './ProfileInfo.module.css';
import Preloader from '../../common/Preloader/Preloader';
import ProfileStatus from './ProfileStatus'
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

function ProfileInfo(props) {
    if(!props.profile) {
        return <Preloader />
    }
    if (props.profile.lookingForAJob) {
        props.profile.lookingForAJob = 'Ищу работу';
    }   else {
        props.profile.lookingForAJob = 'Не ищу работу';
    }
    return (
        <div>
            <div className={s.descriptionBlock}>
                <img src={props.profile.photos.large} alt=""/>
                <h2>{props.profile.fullName}</h2>
                <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/>
                <h4>Обо мне{props.profile.aboutMe}</h4>
                <h4>{props.profile.lookingForAJob}</h4>
                <h4>{props.profile.lookingForAJobDescription}</h4>
                <h4>{props.profile.contacts.github}</h4>
                <h4>{props.profile.contacts.website}</h4>
                <h4>{props.profile.contacts.vk}</h4>
                <h4>{props.profile.contacts.instagram}</h4>
                <h4>{props.profile.contacts.facebook}</h4>
                <h4>{props.profile.contacts.twitter}</h4>
                <h4>{props.profile.contacts.mainLink}</h4>
                <h4>{props.profile.contacts.youtube}</h4>
            </div>
        </div>
    )
}
export default ProfileInfo;

